import React from 'react'
import { Element } from 'react-scroll'
import './Skillcontainer.css'
import ProgressBar from "@ramonak/react-progress-bar";









const Skillcontainer = () => {
  return (
    <Element className='skillcontainer' id='skills'>
        <div className='skillcontainer__img' >
         <img src="mac.jpeg" width="600px" height="300px" alt=''/>
         </div>
           <div className='skillcontainer__txt'>
            <h2>skill set</h2>
            <div className='skillcontainer__skillset'>
              <h5>React</h5>
              <div className='skillcontainer__slider skillcontainer__slider1'>
              <ProgressBar completed={70}/>
              </div>
            </div>
            <div className='skillcontainer__skillset'>
              <h5>Node js</h5>
              <div className='skillcontainer__slider skillcontainer__slider2'></div>
              <ProgressBar completed={90}/>
            </div>
            <div className='skillcontainer__skillset'>
              <h5>Express js</h5>
              <div className='skillcontainer__slider skillcontainer__slider3'></div>
              <ProgressBar completed={60}/>
              <div className='skillcontainer__skillset'>
              <h5>Mongo DB</h5>
              <div className='skillcontainer__slider skillcontainer__slider4'></div>
              <ProgressBar completed={50}/>
            </div>
            </div>









            </div> 

        

    </Element>
  )
}

export default Skillcontainer